<template>
  <div class="contact">
    <div class="form-container">
    <h1>Contact me:</h1>
      <form 
      @reset="onReset"
      @submit="checkForm"
      action="https://formsubmit.co/57d652d6052d877b4e40fd0bb02fcee6"
      method="POST"
      novalidate="true">
      
        <input type="hidden" name="_captcha" value="false">
        <input type="text" name="_honey" style="display:none">
        <input type="hidden" name="_next" value="https://ethanpidge.github.io/#contact">

        <div class="form-section">
          <label>Name:</label><br />
          <input type="text" name="Name" v-model="name" />
        </div>

        <div class="form-section">
          <label>Email:</label><br />
          <input type="text" name="Email" v-model="email" />
        </div>

        <div class="form-section">
          <label>Message:</label><br />
          <textarea rows="4" name="Comment" v-model="message"></textarea>
        </div>
        
        <button type="submit">Submit</button>
        <button type="reset">Reset</button>

        <p class="error-message" v-if="errors.length">
          <b>Please correct the following error(s):</b>
          <ul>
            <li v-bind:key="error" v-for="error in errors">{{ error }}</li>
          </ul>
        </p>

        <p class="success-message" v-if="success.length">
          {{success}}
        </p>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'About',
  data() {
    return {
      errors: [],
      success: "",
      name: "",
      email: "",
      message: "",
    };
  },
  methods: {
    onReset() {
      this.name = "";
      this.email = "";
      this.message = "";
      this.errors = [];
    },
    checkForm: function (e) {
      this.errors = [];
      this.success = "";

      if (!this.name) {
        this.errors.push("Name required.");
      }
      if (!this.message) {
        this.errors.push('Message required.');
      } else if (this.message.length < 10) {
        this.errors.push('Message needs to at least 10 Characters.');
      }
      if (!this.email) {
        this.errors.push('Email required.');
      } else if (!this.validEmail(this.email)) {
        this.errors.push('Valid email required.');
      }

      if (!this.errors.length) {
        this.success='Thank you for sending your message!';
        return true;
      }

      e.preventDefault();
    },
    validEmail: function (email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    }
  }
}
</script>

